import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ResponseService {
  constructor() { }

  getBotResponse(input: string): string {
    if (input === '  ') {
      return '    ';
    } else if (input === ' ') {
      return '  ';
    } else if (input === 'scissors') {
      return 'rock';
    } else {
       return "          "
    }
    
    return '';
  }

}
